import tuiIcon from "@/components/icon/icon"
import tuiNumberbox from "@/components/numberbox/numberbox.vue"
import tuiRate from "@/components/rate/rate"
import hchPoster from '../../wxcomponents/hch-poster/hch-poster.vue'
import tuiCountdown from "@/components/countdown/countdown"
import {
	ajax,
	getSecret
} from '@/common/ajax/ajax.js'
export default {

	onShareAppMessage(res) {
		if (res.from === 'button') { // 来自页面内分享按钮
			console.log(res.target)
		}
		return {
			title: this.goodsData.name,
			imageUrl: this.swiperList[0].img,
			path: 'pages/goods/goods?gid=' + this.goodsData.id,
			success: (res) => {
				console.log(res);
			},
			fail: (res) => {
				console.log(res);
			}
		}
	},
	components: {
		tuiNumberbox,
		tuiRate,
		hchPoster,
		tuiIcon,
		tuiCountdown
	},
	data() {
		return {
			// 商品ID
			id: '',
			secret: '',
			timestamp: '',
			//控制渐变标题栏的参数
			beforeHeaderzIndex: 11, //层级
			afterHeaderzIndex: 10, //层级
			beforeHeaderOpacity: 1, //不透明度
			afterHeaderOpacity: 0, //不透明度
			//是否显示返回按钮



			//轮播主图数据
			swiperList: [{}],
			//轮播图下标
			currentSwiper: 0,
			anchorlist: [], //导航条锚点
			selectAnchor: 0, //选中锚点
			serviceClass: '', //服务弹窗css类，控制开关动画
			specClass: '', //规格弹窗css类，控制开关动画
			shareClass: '', //分享弹窗css类，控制开关动画
			// 商品信息
			goodsData: {
				id: '',
				name: "",
				price: "",
				disPrice: '',
				group_price: '',
				number: 1,
				service: [],
				spec: ["XS", "S", "M", "L", "XL", "XXL"],
				comment: {
					number: 0,
					userface: '../../static/img/face.jpg',
					username: '未上线',
					content: '商品评论还未上线，敬请期待！'
				}
			},
			selectSpec: null, //选中规格
			isKeep: false, //收藏
			//商品描述html
			descriptionStr: '',
			// 购买数量
			value: 1,
			// 好评几颗星
			current: 4,
			xopenid: '',
			deliveryFlag: false,
			canvasFlag: true,
			posterData: {},
			// 是否开始秒杀
			secShow: false,
			endTime: 0,
			// 秒杀价格
			secPrice: 0,
			// 下一场秒杀
			nextShow: false,
			startTime: 0,
			//是否参与开团
			group: '',
			// 开团规则
			rule: [
				'选择商品',
				'支付开团',
				'分享好友',
				'成功(失败退款)'
			],
			groupList: [],
			group_num: '',
			group_gid: '',
			// 优惠券
			couponList:[]
		};
	},
	onLoad(option) {


		//小程序隐藏返回按钮
		this.showBack = false;

		// var text = getSecret();
		// this.secret = text.secret;
		// this.timestamp = text.timestamp;
		this.gid = option.gid;
		// this.getDetails();
		if (option) {
			if (option.type) {
				this.group = option
			}
		}

	},
	onShow() {

		this.getDetails()
		// uni.getStorage({
		// 	key: 'user',
		// 	success: (res) => {
		// 		this.xopenid = res.data.openid;

		// 	}
		// });
	},
	onReady() {
		this.calcAnchor(); //计算锚点高度，页面数据是ajax加载时，请把此行放在数据渲染完成事件中执行以保证高度计算正确
	},
	onPageScroll(e) {
		//锚点切换
		this.selectAnchor = e.scrollTop >= this.anchorlist[2].top ? 2 : e.scrollTop >= this.anchorlist[1].top ? 1 : 0;
		//导航栏渐变
		let tmpY = 375;
		e.scrollTop = e.scrollTop > tmpY ? 375 : e.scrollTop;
		this.afterHeaderOpacity = e.scrollTop * (1 / tmpY);
		this.beforeHeaderOpacity = 1 - this.afterHeaderOpacity;
		//切换层级
		this.beforeHeaderzIndex = e.scrollTop > 0 ? 10 : 11;
		this.afterHeaderzIndex = e.scrollTop > 0 ? 11 : 10;
	},
	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom() {

	},
	mounted() {},
	methods: {
		// 获取商品详情
		getDetails() {
			let data = {
				id: this.gid,
			};
			this.$xm.post('/Product/getInfo', data, (res) => {
				let result = res;
				this.goodsData.id = result.id;
				this.goodsData.price = result.pro_price;
				this.goodsData.name = result.pro_name;
				this.goodsData.disPrice = result.dis_price;
				this.goodsData.group_price = result.group_price;
				this.goodsData.service = result.service;
				this.secPrice = result.sec_price;
				this.group_gid = result.gid
				this.group_num = result.group_num
				this.couponList=result.discount;
				if (result.keep) {
					this.isKeep = true
				} else {
					this.isKeep = false
				}
				if (result.nextstart) {
					this.nextShow = true;
					this.startTime = parseInt(result.nextstart) - Math.round(new Date() / 1000);
				} else {
					this.nextShow = false
				}
				if (result.etime) {
					this.secShow = true;
					this.endTime = parseInt(result.etime) - Math.round(new Date() / 1000);
				} else {
					this.secShow = false;
				}
				if (result.group_price) {
					this.groupData();
				}
				this.swiperList[0].img = 'http://ximiphoto.oss-cn-hangzhou.aliyuncs.com/thumb/' + result.pro_sn +
					'.jpg?x-oss-process=style/600';
				this.descriptionStr = '<div style="text-align:center;"><img width="100%" src="http://img.xmvogue.com/detail/' +
					result.pro_sn + '-.jpg?x-oss-process=style/800"/></div>'
			})

		},
		// 拼团用户
		groupData() {
			let data = {
				gid: this.gid
			}
			this.$xm.post('/groupbuy/get_group_list', data, (res) => {
				if (res) {
					res.forEach((item) => {
						item.num = Number(item.group_num) - Number(item.list.length);
						item.seckillTime = parseInt(item.etime) - Math.round(new Date() / 1000);
					})

					this.groupList = res;

				}
			})
		},
		// 申请参团
		bindGroup(item) {
			// 参团
			let store;
			if (uni.getStorageSync('store').store_code) {
				store = uni.getStorageSync('store').store_code;
			} else {
				store = 10001
			}
			uni.navigateTo({
				url: '../order/confirmation?store=' + store + '&proid=' + this.gid + '&pronum=' + this.value + '&type=join' +
					'&groupid=' + item.geoup_id +
					'&sid=' + item.gid
			})
		},
		// 收藏
		like() {
			let data = {
				proid: this.gid
			};
			this.$xm.post('/Collect/addcollect', data, (res) => {
				if (res.msg == '添加成功!') {
					this.isKeep = true;
					uni.showToast({
						title: '收藏成功'
					})
				}
				console.log(res);
			})
		},
		dellike() {
			uni.showModal({
				title: '确定取消收藏吗？',
				content: '',
				confirmText: '取  消',
				cancelColor: '#b2b2b2',
				confirmText: '确  定',
				confirmColor: '#2c2c2c',
				success: (res) => {
					if (res.confirm) {
						let data = {
							id: this.gid
						}
						this.$xm.post('/Collect/delcollect', data, (res) => {
							console.log(res);
							if (res.code == 2000) {
								this.isKeep = false;
								uni.showToast({
									title: '删除成功'
								})
							}
						})
					}
				},
			})
		},
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		},
		// 调整购买数量
		change(e) {
			this.value = e.value;
			console.log(this.secPrice)
			if (this.secPrice) {
				if (this.value > 1) {
					this.value = 1;
					uni.showToast({
						title: "秒杀商品限购一件！",
						icon: 'none'
					});
				}
			}
			if (this.value < 1) {
				this.value = 1;
				uni.showToast({
					title: "亲，不能再见少了",
					icon: 'none'
				});
			}
		},
		// 购物车
		cart() {
			uni.switchTab({
				url: '../tabBar/cart/cart'
			})
		},
		// 分享
		share() {
			this.shareClass = 'show';
		},
		hideShare() {
			this.shareClass = 'hide';
			setTimeout(() => {
				this.shareClass = 'none';
			}, 150);
		},
		createCanvasImageEvn() {
			this.shareClass = 'hide';
			Object.assign(this.posterData, {
				url: this.swiperList[0].img, //商品主图
				title: this.goodsData.name, //标题
				discountPrice: this.goodsData.disPrice || 0, //折后价格
				orignPrice: this.goodsData.price, //原价
				code: 'pages/details/details?gid=' + this.gid, //小程序码
				id: this.gid,
				close: '../../static/img/close_btn.png'

			})
			this.$forceUpdate(); //强制渲染数据
			setTimeout(() => {
				this.canvasFlag = false; //显示canvas海报
				this.deliveryFlag = false; //关闭分享弹窗
				this.$refs.hchPoster.createCanvasImage(); //调用子组件的方法
			}, 500)

		},
		// 取消海报
		canvasCancel(val) {
			this.canvasFlag = val;
			this.getDetails();
		},
		// 加入购物车
		joinCart() {
			let data = {
				proid: this.gid,
				pronum: this.value,
			};
			this.$xm.post('/Cart/add', data, (res) => {
				let result = res.msg;
				if (res.s == 1) {
					uni.showToast({
						title: result
					});
				} else if (res.s == 0) {
					uni.showToast({
						title: result,
						icon: 'none'
					});
				}
			})

		},
		//立即购买
		buy() {
			this.toConfirmation();
		},
		//商品评论
		toRatings() {
			uni.navigateTo({
				url: './ratings/ratings'
			})
		},
		//跳转确认订单页面
		toConfirmation() {
			let store;
			if (uni.getStorageSync('store').store_code) {
				store = uni.getStorageSync('store').store_code;
			} else {
				store = 10001
			}
			uni.navigateTo({
				url: '../order/confirmation?store=' + store + '&proid=' + this.gid + '&pronum=' + this.value
			})
		},
		//跳转评论列表
		showComments(goodsid) {

		},
		//选择规格
		setSelectSpec(index) {
			this.selectSpec = index;
		},
		//减少数量
		sub() {
			if (this.goodsData.number <= 1) {
				return;
			}
			this.goodsData.number--;
		},
		//增加数量
		add() {
			this.goodsData.number++;
		},
		//跳转锚点
		toAnchor(index) {
			this.selectAnchor = index;
			uni.pageScrollTo({
				scrollTop: this.anchorlist[index].top,
				duration: 200
			});
		},
		//计算锚点高度
		calcAnchor() {
			this.anchorlist = [{
					name: '主图',
					top: 0
				},
				{
					name: '评价',
					top: 0
				},
				{
					name: '详情',
					top: 0
				}
			]
			let commentsView = uni.createSelectorQuery().select("#comments");
			commentsView.boundingClientRect((data) => {
				console.log(data);
				let statusbarHeight = 0;
				//APP内还要计算状态栏高度



				let headerHeight = uni.upx2px(100);
				this.anchorlist[0].top = data.top - headerHeight - statusbarHeight;
				this.anchorlist[1].top = data.bottom - headerHeight - statusbarHeight;

			}).exec();
		},
		//返回上一页
		back() {
			uni.navigateBack();
		},
		//服务弹窗
		showService() {

			this.serviceClass = 'show';
		},
		//关闭服务弹窗
		hideService() {
			this.serviceClass = 'hide';
			setTimeout(() => {
				this.serviceClass = 'none';
			}, 200);
		},
		//规格弹窗
		showSpec(fun) {

			this.specClass = 'show';
			this.specCallback = fun;
		},
		specCallback() {
			return;
		},
		//关闭规格弹窗
		hideSpec() {
			this.specClass = 'hide';
			//回调

			this.selectSpec && this.specCallback && this.specCallback();
			this.specCallback = false;
			setTimeout(() => {
				this.specClass = 'none';
			}, 200);
		},
		discard() {

		},
		// 参加团购
		joinGroup() {
			let store;
			if (uni.getStorageSync('store').store_code) {
				store = uni.getStorageSync('store').store_code;
			} else {
				store = 10001
			}
			// 参团

			if (this.group.type == 'join') {
				uni.navigateTo({
					url: '../order/confirmation?store=' + store + '&proid=' + this.gid + '&pronum=' + this.value + '&type=' + this.group
						.type + '&groupid=' + this.group.groupid +
						'&sid=' + this.group_gid
				})
			} else {
				// 开团
				uni.navigateTo({
					url: '../order/confirmation?store=' + store + '&proid=' + this.gid + '&pronum=' + this.value + '&type=' + this.group
						.type +
						'&sid=' + this.group_gid
				})
			}

		}
	}
};
